import autotime from '@/utils/autotime';
export default {
  props: {
    value: {}
  },
  data: function data() {
    return {
      btn: 0
    };
  },
  computed: {
    dates: {
      // 在子组件内用sData接收父组件的绑定的v-model值
      get: function get() {
        return this.value;
      },
      set: function set(val) {
        this.btn = 0;
        this.$emit('input', val);
        this.$emit('change', val);
      }
    }
  },
  methods: {
    changeSelect: function changeSelect(btn) {
      autotime.reloadTime();

      switch (btn) {
        case 1:
          this.dates = [autotime.y30day, autotime.yday];
          break;

        case 2:
          this.dates = [autotime.y7day, autotime.yday];
          break;

        case 3:
          this.dates = [autotime.yday, autotime.yday];
          break;
      }

      this.btn = btn;
    }
  }
};